import React from "react";
import "./App.css";
let marked = require("marked");

const PLACEHOLDER =
  "# this is an h1\n\n## and this is an h2\n\nwhile this is a [link]('#')\n\nand if this is some inline `<code>`\n```\n// then this is\n   multi\n   line\n   code\n```\n\n![images](https://#)\n\nbut **there is more**\n- a list item\n\n>a blockquote";

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = { output: PLACEHOLDER };
  }

  markIt = (output) => {
    this.setState({ output });
  };

  render() {
    return (
      <div className="container">
        <h1 id="containerHeading">
          Welcome to my simple React Markdown Previewer App
        </h1>
        <h2 className="heading">This is the editor</h2>
        <textarea
          id="editor"
          className="input"
          rows="20"
          onChange={(e) => {
            this.markIt(e.target.value);
          }}
        >
          {PLACEHOLDER}
        </textarea>
        <h2 className="heading">This is the Markdown Previewer</h2>
        <div
          id="preview"
          className="output"
          dangerouslySetInnerHTML={{ __html: marked(this.state.output) }}
        ></div>
      </div>
    );
  }
}

export default App;
